@import url("https://fonts.googleapis.com/css2?family=Sora:wght@100;200;300;400;500;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Great+Vibes&display=swap");
body,
* {
  box-sizing: border-box;
  /*margin: 0;
    padding: 0;*/
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100vh;
}

h1 {
  font: 700 3em "Great Vibes", cursive;
  margin: 0;
  margin-bottom: 2rem;
}

.board {
  display: grid;
  grid-template: repeat(3, 20vh)/repeat(3, 20vh);
  margin-bottom: 2rem;
}
.board .cell {
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font: 700 5rem sans-serif;
  user-select: none;
  transition: color 0.2s;
}
.board .cell.end-game-cell {
  background-color: black;
}
.board .cell.empty {
  color: transparent;
}
.board .cell.empty:hover {
  color: rgba(0, 0, 0, 0.5);
}
.board .cell:nth-child(1) {
  border-top: none;
  border-left: none;
}
.board .cell:nth-child(2) {
  border-top: none;
}
.board .cell:nth-child(3) {
  border-top: none;
  border-right: none;
}
.board .cell:nth-child(4) {
  border-left: none;
}
.board .cell:nth-child(6) {
  border-right: none;
}
.board .cell:nth-child(7) {
  border-bottom: none;
  border-left: none;
}
.board .cell:nth-child(8) {
  border-bottom: none;
}
.board .cell:nth-child(9) {
  border-bottom: none;
  border-right: none;
}

.game-msg-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.game-msg-container .game-msg {
  text-align: center;
  font: 500 1.5em Verdana, Geneva, Tahoma, sans-serif;
  margin: 0;
  margin-bottom: 1rem;
}
.game-msg-container .restart-btn {
  background: none;
  border: 3px black solid;
  border-top: 0;
  padding: 0.25rem 1rem;
  font: 400 0.9em Verdana, Geneva, Tahoma, sans-serif;
  transition: all 0.5s;
}
.game-msg-container .restart-btn:hover {
  box-shadow: inset 20rem 0 0 0 black;
  color: #fff;
}

@media all and (max-width: 600px) {
  .board {
    grid-template: repeat(3, 30vw)/repeat(3, 30vw);
  }
}